import React, {useState} from 'react';
import {Container, Box, TextField, ButtonGroup, Button} from '@mui/material';
import {enc} from 'crypto-js';
import ContentCopyAdornment from '../compontents/ContentCopyAdornment';

export default function Base64() {
  const [plain, setPlain] = useState('');
  const [target, setTarget] = useState('');
  const encode = () => {
    const str = enc.Base64.stringify(enc.Utf8.parse(plain));
    setTarget(str);
  };
  const decode = () => {
    const str = enc.Utf8.stringify(enc.Base64.parse(plain));
    setTarget(str);
  };
  return (
    <Container>
      <Box sx={{mt: 2}}>
        <TextField
          label={'文本'}
          value={plain}
          onChange={event => {
            setPlain(event.target.value);
          }}
          fullWidth
          multiline
        />
      </Box>
      <Box sx={{mt: 2}}>
        <ButtonGroup>
          <Button onClick={encode}>编码</Button>
          <Button onClick={decode}>解码</Button>
        </ButtonGroup>
      </Box>
      <Box sx={{mt: 2}}>
        <TextField
          label={'结果'}
          value={target}
          fullWidth
          multiline
          InputProps={{
            endAdornment: <ContentCopyAdornment content={target} />,
          }}
        />
      </Box>
    </Container>
  );
}
